.MoviesGallery {
    padding-top: 10px;
    padding-bottom: 127px;
}

.MoviesGalleryList {
    margin-left: -30px;
    margin-bottom: -30px;
    display: flex;
    flex-wrap: wrap;
    margin-top: 30px;
    list-style: none;
}

.MoviesGalleryItem {
    flex-basis: calc(100% / 3 - 30px);
    width: 274px;
    margin-left: 30px;
    margin-bottom: 30px;
    border-radius: 5px;
    transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.MoviesGalleryItem:hover {
    transform: scale(1.03);
    box-shadow: 0px 3px 1px -2px rgba(0, 0, 0, 0.2),
        0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 1px 5px 0px rgba(0, 0, 0, 0.12);
    cursor: pointer;

}

.GalleryItemMovies {
    text-decoration: none;
    font-weight: bold;
    width: 100%;
    height: 260px;
    object-fit: cover;
    transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.GalleryItemMovies:hover {
    transform: scale(1.03);
    cursor: zoom-in;
}

.movieTitle {
    color: black;

}

.titleWrapper {
    text-align: center;
    padding-top: 20px;
    padding-bottom: 20px;

}